import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Projectos from '@/components/Projectos'
import Sobre from '@/components/Sobre'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/projectos',
      name: 'projectos',
      component: Projectos
    },
    {
      path: '/sobre',
      name: 'sobre',
      component: Sobre
    }
  ]
})



