<template>


<div id="projectos" class="ui grid">

    <div class="one column">

        <h1>Projectos</h1>

        <div class="ui special cards">
  

    <div class="card" v-for="projecto in projectos" :key="projecto.id">
    <div class="blurring dimmable image">
      <div class="ui dimmer">
        <div class="content">
          <div class="center">
            <div class="ui inverted button">Add Friend</div>
          </div>
        </div>
      </div>
      <img src="http://lorempixel.com/400/200">
    </div>
    <div class="content">
      <a class="header">{{projecto.subject}}</a>
      <div class="meta">
        <span class="date">Feito em {{projecto.year}}</span>
      </div>
    </div>
    <div class="extra content">
      <a>
        <i class="users icon"></i>
        Professor {{projecto.professor}}
        <br>
        
      </a>
    </div>
  </div>


  </div>

    </div>
</div>


</template>


<script>

import db from './firebaseInit'

export default {

    name: 'projectos',
    data(){
        return{
            projectos:[]

        }
    },
    created(){
        
        db.collection('projectos').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {

               // console.log(doc.data());
                const data = {
                    'id':doc.id,
                    'subject':doc.data().subject,
                    'professor':doc.data().professor,
                    'year':doc.data().year,
                    'image':doc.data().image
                }
                this.projectos.push(data)
            })
        })
    }
}
</script>


